body {
    background-color: black;
    overflow-y: hidden;
    overflow-x: hidden;
    font-family: 'Nova Square' !important;
}

.nav {
    position: fixed;
    height: 8vh;
    width: 100vw;
    top: 0px;
    background-color: black;
    z-index: 100;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
}

.nav__ctf {
    width: 210px !important;
}

.nav__ctf img {
    padding: 10px;
    width: 60px !important;
}

.nav__buttons-container {
    width: 100%;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.nav__button {
    margin: auto 16px;
    font-weight: bold;
    font-size: 22px;
    color: white;
}

.nav__button:hover {
    cursor: pointer;
    color: green;
}

.nav__score {
    width: 210px;
    color: white;
    font-size: 18px;
    cursor: cell;
}
.social-media-icons-container {
    position: fixed;
    bottom: 1%;
    left: 1%;
    z-index: 10;
    
    display: flex;
    flex-direction: row;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.icons {
    margin: 9px;
}

.icon:hover {
    cursor: pointer;
}
.leaderboard-container {
    position: absolute;
    top: 8vh;
    height: 92vh;
    width: 100vw;
    z-index: 10;

    color: white;
    background-color: rgba(10, 10, 10, 0.8);

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.leaderboard-card-container {
    align-self: center;
    width: 40vw;
    max-height: 60vh;

    overflow-y: scroll;
    overflow-x: hidden;
    scrollbar-width: 0px;
}

table{
    width: 100%;
    height: 100%;
    /* border: 2px solid black; */
    text-align: center;
    border-collapse: separate;
}

table th{
    font-weight: bold;
    text-align: center;
    font-size: 18px;
}

.table-one {
    height: 10px;
}

tr{
    height: 50px;
    font-size: 15px;
}

.empty-td {
    width: 30%;
}
.ReactModalPortal > div {
    opacity:0;
}

.ReactModalPortal .ReactModal__Overlay{
    display: flex;
    justify-content:center;
    align-items:center;
    -webkit-transition: opacity 200ms ease-in-out;
    transition: opacity 200ms ease-in-out;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
    opacity: 1;
}


.ReactModalPortal .ReactModal__Overlay--before-close {
    opacity: 0;
}

.modal{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    background-color: white;
    padding: 15px;
    border-radius: 8px;
    font-family: 'Nova Square';
    width: 24vw;
    height: 42vh;
}

.modal_title_container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    /* border: 2px solid red; */
}

.modal__title {
    font-weight: 500;
}

.modal_close_button {
    /* top: 1px;
    right: 1px; */
    margin-left: 0px;
    font-size: 18px;
    cursor: pointer;
}

.modal__question {
    text-align: left;
    margin-top: 10px;
    margin-bottom: 10px;
    /* height: 0px; */
}

.modal__link {
    color: blue;
    text-align: left;
    margin-top: 10px;
    margin-bottom: 10px;
}

.modal__answer-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    margin-bottom: 10px;
}

.modal__answer__input {
    /* outline: none; */
    border: 1px solid black;
    border-radius: 5px;
    padding: 7px;
    font-size: 15px;
    width: 100%;
    font-family: 'Nova Square';
}

.modal__answer__button {
    display: flex;
    justify-content: center;
    align-self: center;
    /* margin: auto; */
    background-color: black;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 8px;
    margin-left: 10px;
    border-radius: 5px;
    cursor: pointer;
}

img {
    height: 80%;
    width: 80%;
}

.modal__hint {
    margin-top: 10px;
    margin-bottom: 10px;
    text-align: center;
    width: 100%;
    font-size: 14px;
    cursor: pointer;
}

